.farm-card{
    /* width:30%; */
    margin:40px 0;
    padding-bottom:40px;
    position:relative;
   
    
}
.middle {
    -webkit-transition: .5s ease;
    transition: .5s ease;
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    text-align: center;
  }
  .farm-card:hover img{
      opacity:.4;
      
  }
  
.farm-card:hover .middle{
    opacity:1;

}
